import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import data from './data.js';
import { lazy, Suspense, useState, useEffect, createContext } from 'react';
import { Button, Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import { useQuery } from 'react-query';

// 메인페이지에서 불러올 필요없는 컴포넌트는 lazy로 요청 시 로딩
// 메인페이지 로딩 속도 향상 장점
// 로딩시간 발생의 단점이 있음. => Suspense 컴포넌트로 로딩페이지 기능.
const Detail = lazy(() => import('./routes/Detail.js'));
const Cart = lazy(() => import('./routes/Cart.js'));


// export let Context1 = createContext(); 외부라이브러리 redux로 대체가능
// <Context1.Provider value={ {stock, shoes} }> 
//   state 보낼 컴포넌트를 감싼다 
// </Context1.Provider>

function App() {

  
  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  
  let [click, setClick] = useState(2);
  let [loading, setLoading] = useState(false);
  // let [stock, setStock] = useState([10, 11, 12]);
  
  let getItem = JSON.parse(localStorage.watched);
  
  useEffect(()=>{
    if(getItem.length == 0){
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])


  // react-query 라이브러리
  // 1. 성공/실패/로딩 중 쉽게 파악 가능
  // 2. 틈만나면 refetch 해줌
  // 3. 실패 시 retry 해줌
  // 4. state 공유 안해도 된다.(ajax코드 다시 쓰면 됨)
  // 5. ajax 결과 캐싱 가능
  // => 실시간 데이터 전송이 필요한 페이지에서 주로 쓰인다.
  
  let result = useQuery('userData', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json')
    .then((a)=> {
      return a.data
    }),
    { staleTime : 2000 } // refetch 간격 조절 가능
  })



  return (
    <div className="App">

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand
            onClick={() => { navigate('/') }}
            className="logo">ShopRY</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/about') }}>About</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail') }}>Products</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            { result.isLoading ? '로딩중' : result.data.name }
            { result.error && 'error' }
          </Nav>
        </Container>
      </Navbar>
      
      {/* suspense로 로딩중 화면 구현 */}
      <Suspense fallback={<div>로딩중</div>}> 
        <Routes>
          <Route path="/" element={
            <>
              <div className="main-bg">
                <div className="watched">
                  <p>최근 본 상품
                    {
                      getItem.map((a, i)=>{
                        return(
                          <span key = {i}> {a} </span>
                        )
                      })
                    }
                  </p>
                </div>
              </div>
              <button onClick={() => {
                let copy = [...shoes];
                copy.sort((a, b) =>
                  a.title < b.title ? -1 : 1
                )
                setShoes(copy);
              }}>이름 순 정렬</button>
              <div className="card-con">
                <div className="card-con2">
                  {
                    shoes.map((a, i) => {
                      return (
                        < Card a={a} key={i} />
                      )
                    })
                  }
                </div>
              </div>
              {
                loading ? < Loading /> : null
              }
              {
                click < 4 ?
                  <button onClick={() => {
                    setClick(click + 1);
                    setLoading(true);

                    axios.get('https://codingapple1.github.io/shop/data' + click + '.json')
                      .then((result) => {
                        let newData = result.data;
                        setLoading(false);

                        let copy = [...shoes, ...newData];
                        setShoes(copy);
                      })
                      .catch(() => {
                        console.log('데이터 로딩 실패');
                        setLoading(false);
                      })

                    // axios라이브러리는 자동으로 json 형식을 array나 object로 변환해준다.
                    // fetch()를 사용하게되면 .then(data => data.json()) 과정 필요함.

                    // 동시에 ajax요청 여러개 날리려면 
                    // Promise.all([ axios.get('/url'), axios.get('/url') ])
                    // .then(()=>{})

                    // axios.post('/url', { name : kim })

                  }}> 더보기 </button>
                  : null
              }
            </>
          } />
            <Route
              path="/detail/:id"
              element={
                  <Detail shoes={shoes} />
              }>
            </Route>

            {/* nested route 하위페이지 접속시엔 상위 element도 보여줌(Outlet으로 위치 정해줘야댐) */}
            <Route path="/about" element={< About />}>
              <Route path="member" element={<div>멤버정보</div>} />
              <Route path="location" element={<div>위치정보</div>} />
            </Route>

            <Route path="/event" element={< Event />}>
              <Route path="two" element={<p>생일쿠폰받기</p>}></Route>
            </Route>

            <Route path="/cart" element={<Cart />}></Route>
            <Route path="*" element={<div>404</div>} />
        </Routes>
      </Suspense>
    </div>
  );

}

function Loading() {
  return (
    <div className="d-flex justify-content-center">
      <div className="spinner-border" role="status"></div>
    </div>
  )
}

function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}

function About() {
  return (
    <div>
      <h4>회사정보입니다.</h4>
      <Outlet></Outlet>
    </div>
  );
}


function Card({ a }) {
  let navigate = useNavigate();
  let src = `https://codingapple1.github.io/shop/shoes${a.id + 1}.jpg`

  return (
    <div className="product">
      <img src={src} width="80%"
        onClick={() => {
          navigate(`/detail/${a.id}`);
        }} />
      <h4>{a.title}</h4>
      <p>{a.price}</p>
    </div>
  )
}

export default App;
